


































































.auth-form {
  width: 480px;
  max-width: calc(100% - 30px);
  background: #fff;
  margin: 0 auto;
  padding: 15px;
  margin-top: 150px;
  margin-bottom: 50px;
  border-radius: 7px;
  box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.1);
}
.auth-form h1 {
  font-size: 30px;
  font-weight: 400;
}
.auth-form > div {
  margin-top: 15px;
}
.auth-form input {
  height: 32px;
  background: #e4e4e4;
  border: none;
  border-radius: 3px;
  margin-bottom: 20px !important;
  font-size: 14px !important;
  padding-left: 10px;
}

.auth-form label,
.auth-form input {
  display: block;
  width: 100%;
  margin-bottom: 8px;
  font-size: 18px;
}

.auth-form input:focus {
  outline: none;
}

.auth-form button {
  font-size: 18px;
  background: #fff;
  padding: 4px 6px;
  margin: 0;
  border-radius: 4px;
  cursor: pointer;
  color: black;
  height: 40px;
  font-size: 18px;
  border: none;
  background: #cfd8dc;
  transition: background-color 0.2s;
  margin-top: 10px;
}

.auth-form button:hover {
  background: #b0bec5;
}

.auth-form button.primary {
  background: #66bb6a;
  color: white;
}

.auth-form button.primary:hover {
  background: #43a047;
}

.auth-form button.single {
  width: 100%;
  margin-top: 15px;
}

.auth-form button:focus {
  outline: none;
}

.auth-form .btn-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.auth-form .btn-group button {
  width: 48%;
}

.auth-form button.disabled {
  background: #ccc;
  color: #999;
  cursor: default;
}

.auth-form .signup-submit {
  width: 100%;
}
.auth-form hr {
  margin-top: 20px;
}
.auth-form .social-providers {
  text-align: center;
}
.auth-form .social-providers a {
  color: #41b883;
  font-size: 36px;
  padding: 4px;
}

@media (max-width: 1000px) {
  .auth-form {
    margin-top: 80px;
  }
}

@media (max-width: 749px) {
  .auth-form {
    margin-top: 40px;
  }
}



