



































































header {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 1;
  height: 100px;
  background: white;
  box-shadow: 0 0 10px 1px rgba(161, 161, 161, 0.37);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 10px 30px;
}

.header-title {
  font-size: 20px;
  width: 425px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  margin-right: 50px;
}

.header-links {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-right: 30px;
}

.header-links a {
  color: black;
  text-decoration: none;
  margin: 5px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
}

.header-links a:first-child {
  margin-right: 20px;
}

.header-links i {
  font-size: 18px;
  margin-left: 5px;
  transform: translateY(2px);
}
.header-links span {
  text-decoration: underline;
}

.header-links a:hover span {
  text-decoration: none;
}

.header-user {
  margin-left: auto;
}

.user-title {
  margin-right: 10px;
  max-width: 250px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.header-user {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.sign-out-button {
  font-size: 24px;
  margin-left: 5px;
  color: black;
}

.sign-out-button.hidden {
  display: none;
}

@media (max-width: 1000px) {
  .header-title {
    font-size: 18px;
    width: 210px;
    margin-right: 45px;
  }

  .header-links {
    margin-right: 0;
  }

  .header-links a {
    margin-right: 20px;
  }
}

@media (max-width: 749px) {
  header {
    padding: 10px 15px;
  }

  .header-title {
    font-size: 16px;
    width: 190px;
    margin-right: 25px;
  }

  .header-links {
    width: 28px;
  }

  .header-links a {
    margin: 8px 0;
  }

  .header-links span {
    display: none;
  }

  .header-links i {
    font-size: 23px;
  }
}

@media (max-width: 530px) {
  .user-title {
    display: none;
  }
}



